@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Asap&display=swap);
body{margin:0;padding:0;box-sizing:border-box;font-family:'Nunito', sans-serif;font-style:normal;font-weight:400}

.button{font-family:'Nunito', sans-serif;font-style:normal;font-weight:400;font-size:16px;line-height:26px;margin:0;padding:0;width:100px;height:34px;border-radius:80px;border:transparent;cursor:pointer}.disabled{background:#B4B4B4;color:rgba(255,255,255,0.87)}.active{background-color:#F4E041;color:rgba(0,0,0,0.87)}.active:hover{background-color:#FFE302}

.header{box-sizing:border-box;height:60px;padding:13px 0;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;background-color:#fff}@media (max-width: 1169px){.header{padding:13px 60px}}@media (max-width: 1023px){.header{padding:13px 32px}}@media (max-width: 767px){.header{padding:13px 16px}}.header__actions{display:-webkit-flex;display:flex;grid-gap:10px;gap:10px}

.container{max-width:1170px;margin:0 auto}.page__header{background-color:#FFF}.page__main{background-color:#E5E5E5}

.main{height:650px;background-image:url(../../static/media/image_big.8e3a7e0d.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;color:#fff;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;text-align:center;margin-bottom:140px}@media (max-width: 768px){.main{background-image:url(../../static/media/image_tablet.1771b24c.jpg);height:500px}}@media (max-width: 360px){.main{background-image:url(../../static/media/image_mobile.0252af7d.jpg)}}.main__box{width:380px;padding:164px 0 0}@media (max-width: 1023px){.main__box{padding:89px 0 0}}@media (max-width: 767px){.main__box{width:328px;padding:40px 0 0}}.main__title{font-size:40px;line-height:40px;margin-bottom:21px}.main__content{font-size:16px;line-height:26px;margin-bottom:32px}

.card{box-sizing:border-box;width:282px;border-radius:10px;padding:20px;background-color:#fff}@media (max-width: 1023px){.card{width:344px}}@media (max-width: 767px){.card{width:328px}}.card__img,.card__name{margin-bottom:20px}.card__name,.card__info{font-size:16px;line-height:26px}.card__img{border-radius:50%}.card__phone-link,.card__mail-link{text-decoration:none;color:rgba(0,0,0,0.87)}.card__name,.card__mail{position:relative;cursor:pointer}.card__phone{cursor:pointer}.card__name::after,.card__mail::after{position:absolute;padding:3px 16px;border-radius:4px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);bottom:-35px;width:-webkit-max-content;width:max-content;background-color:rgba(0,0,0,0.87);color:#fff;content:attr(data-tooltip);opacity:0;z-index:-1}.card__name:hover::after,.card__mail:hover::after{content:attr(data-tooltip);opacity:1;z-index:1}

.cards{text-align:center}.cards__title{margin:0 0 50px;font-size:40px;line-height:40px;padding:0 16px}.cards__list{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;grid-gap:29px;gap:29px;margin-bottom:50px}@media (max-width: 767px){.cards__list{-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px}}@media (max-width: 1023px){.cards__list{grid-gap:16px;gap:16px}}

.file{box-sizing:border-box;display:block;border:1px solid #D0CFCF;border-radius:4px;height:54px;padding:14px 0 14px 99px;color:#7E7E7E;font-size:16px;line-height:26px;position:relative}.file__input{display:none}.file::before{content:'Upload';box-sizing:border-box;position:absolute;top:-1px;left:-1px;width:83px;height:54px;border:1px solid rgba(0,0,0,0.87);border-radius:4px 0px 0px 4px;color:rgba(0,0,0,0.87);text-align:center;line-height:54px}.filled{color:rgba(0,0,0,0.87)}.errorFile{border:2px solid #CB3D40;margin-bottom:4px}.errorFile__msg{display:block;padding-left:16px;color:#CB3D40;font-family:'Asap', sans-serif;font-style:normal;font-weight:400;font-size:12px;line-height:14px}.errorFile::before{content:'Upload';box-sizing:border-box;position:absolute;top:-2px;left:-2px;width:83px;height:54px;border:2px solid #CB3D40;border-radius:4px 0px 0px 4px;text-align:center;line-height:54px}

.input{box-sizing:border-box;width:100%}.input__field{box-sizing:border-box;width:100%;margin-bottom:4px;padding:14px 16px;height:54px;border:1px solid #D0CFCF;border-radius:4px;background:#F8F8F8;outline:none;font-family:'Nunito';font-style:normal;font-weight:400;font-size:16px;line-height:26px;color:rgba(0,0,0,0.87)}.input__helper{font-size:12px;line-height:14px;color:#7E7E7E;padding-left:16px}

.radio-input__radio{font-size:16px;line-height:26px;color:rgba(0,0,0,0.87);position:relative;padding-left:32px;cursor:pointer}.radio-input__radio-input{display:none}.radio-input__radio-input:checked ~ .radio-input__radio-span::after{-webkit-transform:translate(-50%, -50%) scale(1);transform:translate(-50%, -50%) scale(1)}.radio-input__radio-input:checked ~ .radio-input__radio-span{border-color:#00BDD3}.radio-input__radio-span{box-sizing:border-box;display:block;height:20px;width:20px;border-radius:50%;border:1px solid #D0CFCF;position:absolute;top:0;left:0}.radio-input__radio-span::after{content:'';width:10px;height:10px;border-radius:50%;display:block;background-color:#00BDD3;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%) scale(0);transform:translate(-50%, -50%) scale(0)}

.register{padding:140px 0 100px}.register__title{margin:0 0 50px;padding:0 16px;font-size:40px;line-height:40px;text-align:center}.register__data{width:380px;margin:0 auto}@media (max-width: 415px){.register__data{width:328px}}.register__forms{display:-webkit-flex;display:flex;grid-gap:50px;gap:50px;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-bottom:25px}.register__position-title{margin-bottom:11px}.register__position-list{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:7px;gap:7px;margin-bottom:47px}.register__upload{margin-bottom:50px}.register__signup{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

